
body{
    
    background-color: #001200;
    color: white;
    font-family: 'Press Start 2P', cursive;
    font-size: 1em;
    
    /* make text super sharp */
    -webkit-font-smoothing: none;
}

li {
    margin-top:  10px;
    list-style: none;
}


.start {
    -webkit-animation-name: startLook;
            animation-name: startLook;
    -webkit-animation-duration: 800ms;
            animation-duration: 800ms;  
    -webkit-animation-iteration-count: infinite;  
            animation-iteration-count: infinite;
}


.mainTitle {
     top:0px;
    -webkit-animation-name: intro;
            animation-name: intro;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;  
    -webkit-animation-iteration-count: infinite;  
            animation-iteration-count: infinite;
}



@-webkit-keyframes startLook {
    
     0%   {opacity: 0}
    100% {opacity: 1}
    
}



@keyframes startLook {
    
     0%   {opacity: 0}
    100% {opacity: 1}
    
}

@-webkit-keyframes intro {
    
    0% {
        
        top:0px;
        color:red;
        
    }
    
    50% {
        top:30px;
        color: white;
    }
    
     
    100% {
        
        top:0px;
        color:red;
        
    }
}

@keyframes intro {
    
    0% {
        
        top:0px;
        color:red;
        
    }
    
    50% {
        top:30px;
        color: white;
    }
    
     
    100% {
        
        top:0px;
        color:red;
        
    }
}


